// import logo from './logo.svg';
import "./App.css";
import { Alert } from "./components/Alert";
import { ExpenseFrom } from "./components/ExpenseFrom";
import { ExpenseList } from "./components/ExpenseList";
import uuid from "react-uuid";
import React, { useState ,useEffect} from "react";

// const initialExpenses = [
//   { id: uuid(), charge: "rent", amount: 1600 },
//   { id: uuid(), charge: "car-payment", amount: 400 },
//   { id: uuid(), charge: "credit-card-bill", amount: 1200 },
// ];
const initialExpenses = localStorage.getItem("expenses")? JSON.parse(localStorage.getItem("expenses"))  : [];
function App() {
  // ****************state variables********************
  const [expenses, setExpenses] = useState(initialExpenses);

  const [charge, setCharge] = useState("");
  const [amount, setAmount] = useState("");
  const [alert, setAlert] = useState({ show: false });

  const [edit, setEdit] = useState(false);

  const [id, setId] = useState(0);


  useEffect(() => {
    localStorage.setItem("expenses" , JSON.stringify(expenses));
  }, [expenses])
  

  // ****************functionality********************

  const chargeHandler = (e) => {
    console.log(e.target.value);
    setCharge(e.target.value);
    // setExpenses((preVal)=>{
    //   return [...preVal ,  ]

    // })
  };
  const amountHandler = (e) => {
    console.log(e.target.value);
    setAmount(e.target.value);
  };

  const alertHandler = ({ type, text }) => {
    setAlert({ show: true, type, text });
    setTimeout(() => {
      setAlert({ show: false });
    }, 3000);
  };

  const submitHandler = (e) => {
    e.preventDefault();
    // console.log(charge , amount)
    if (charge !== "" && amount > 0) {
      if (edit) {
        let tempExpense = expenses.map((item) => {
          return item.id === id ? { ...item, charge, amount } : item;
        });
        setExpenses(tempExpense);
        setEdit(false);
        alertHandler({ type: "success", text: "Item Edited" });
      } else {
        const singleExpense = { id: uuid(), charge, amount };
        setExpenses([...expenses, singleExpense]);
        alertHandler({ type: "success", text: "Item Submited" });
      }

      setCharge(" ");
      // setAmount(" ");
      setAmount("");
    } else {
      alertHandler({ type: "danger", text: "somthing went wrong!" });
    }
  };

  const editHandler = (id) => {
    let editTemp = expenses.find((item) => item.id === id);
    let { charge, amount } = editTemp;
    // console.log(editTemp);
    setAmount(amount);
    setCharge(charge);
    setEdit(true);
    setId(id);
  };

  const deleteHandler = (id) => {
    // console.log(" deleted");
    let tempExp = expenses.filter((item) => item.id !== id);
    setExpenses(tempExp);
  };

  const clearItem = () => {
    setExpenses([]);
  };

  return (
    <>
      {alert.show && <Alert type={alert.type} text={alert.text} />}
      <Alert />
      <h1>Budget calculator</h1>
      <main className="App">
        <ExpenseFrom
          charge={charge}
          amount={amount}
          chargeHandler={chargeHandler}
          amountHandler={amountHandler}
          submitHandler={submitHandler}
          edit={edit}
        />
        <ExpenseList
          expense={expenses}
          editHandler={editHandler}
          deleteHandler={deleteHandler}
          clearItem={clearItem}
        />
      </main>
      <h1>
        {" "}
        Total Spending :{" "}
        <span className="total">
          $
          {expenses.reduce((acc, cur) => {
            return (acc += parseInt(cur.amount));
          }, 0)}
        </span>
      </h1>
    </>
  );
}

export default App;
