import React from "react";
import { ExpenseItem } from "./ExpenseItem";
import { MdDelete } from "react-icons/md";

export const ExpenseList = ({ expense , editHandler , deleteHandler , clearItem}) => {
  return (
    <div>
      <ul className="list">
        {expense.map((expense) => {
          return <ExpenseItem key={expense.id} expense={expense}  editHandler={editHandler} deleteHandler={deleteHandler} />;
        })}
      </ul>
      {expense.length > 0 && (
        <button className="btn"  onClick={clearItem}>
          {" "}
          clear Expense <MdDelete className="btn-icon" />{" "}
        </button>
      )}
    </div>
  );
};
