import React from 'react'
import {MdEdit} from 'react-icons/md'
import {MdDelete} from 'react-icons/md'



export const ExpenseItem = ({expense ,editHandler , deleteHandler}) => {
  const {id,charge,amount} = expense
  return (
    <>
  <li className='item'>
    <div className='info'>
      <span className='charge'>{charge}  </span>
      <span className='amount'>${amount} </span>
</div>
<div>
  <button className='edit-btn'  onClick={()=>editHandler(id)} > <MdEdit/></button>
  <button className='clear-btn' onClick={()=>deleteHandler(id)}> <MdDelete/></button>

</div>
  </li>
    
    
    </>
  )
}
